import * as types from "../actionTypes"
import {
  beginTicketValidation,
  genRandomUser,
  register,
  submitComment,
  deleteComment,
  deleteUser
} from "./validationHelpers"

const invalid_auth_token =
  "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..q9z_tG7gEqaRMfrbTpj9Jz52vocqOBWgEpCd3KC6giI"

// the martian
const movie_id = "573a13eff29313caabdd82f3"
export function validateDeleteComments() {
  return async dispatch => {
    try {
      dispatch(beginTicketValidation("DeleteComments"))
      let testUser = genRandomUser()
      const registerResponse = await register(testUser)
      const { auth_token } = registerResponse.json
      let postC, badC, goodC
      const commentResponse = await submitComment(
        movie_id,
        "feefee",
        auth_token
      )
      if (!commentResponse.ok) {
        throw new Error("Unable to post a comment")
      } else {
        postC = true
      }
      const firstCommentID = commentResponse.json.comments[0]._id
      let badResponse = await deleteComment(
        firstCommentID,
        invalid_auth_token,
        movie_id
      )
      if (badResponse.ok) {
        throw new Error("Was able to update a comment that wasn't owned")
      } else {
        badC = true
      }

      let goodUpdate = await deleteComment(firstCommentID, auth_token, movie_id)
      if (!goodUpdate.ok) {
        throw new Error("Unable to update comment")
      } else {
        goodC = true
      }

      if (postC && badC && goodC) {
        deleteUser(auth_token, testUser)
        return dispatch(validateDeleteCommentsSuccess())
      } else {
        return dispatch(
          validateDeleteCommentsError(
            new Error("The return from the api was incorrect")
          )
        )
      }
    } catch (e) {
      return dispatch(validateDeleteCommentsError(new Error(e.message)))
    }
  }
}

export function validateDeleteCommentsSuccess() {
  return { type: types.VALIDATE_DELETE_COMMENTS_SUCCESS }
}

export function validateDeleteCommentsError(error) {
  return { type: types.VALIDATE_DELETE_COMMENTS_ERROR, error }
}



